import React, {useCallback, useContext, useEffect, useState} from "react";
import MainData from "./components/PageBlocks/MainData/MainData";
import ContactDetails from "./components/PageBlocks/ContactDetails/ContactDetails";
import PersonalData from "./components/PageBlocks/PersonalData/PersonalData";
import WorkExperience from "./components/PageBlocks/WorkExpirience/WorkExperience";
import Education from "./components/PageBlocks/Education/Education";
import CoursesAndTrainings from "./components/PageBlocks/CoursesAndTrainings/CoursesAndTrainings";
import LanguageSkills from "./components/PageBlocks/LanguageSkills/LanguageSkills";
import ComputerSkills from "./components/PageBlocks/ComputerSkills/ComputerSkills";
import AdditionalInformation from "./components/PageBlocks/AdditionalInformation";
import "./App.css"
import {EmptyInputs} from "./context/context";
import {
    validateContactDetails,
    validateEmail,
    validateMainData,
    validatePersonalData,
    validatePhoneNumber,
    validateAllFields
} from "./helpers/validators";
function App() {

    const initialData = {
        name: "",
        surname: "",
        patronymic: "",
        workPosition: "", // SOOBSHIT' RAVSHANU
        expectedSalary: "",
        busyness: "Частичная",
        workingSchedule: "Гибкий график",
        readinessOfRelocation:"",
        readinessOfBusinessTrip: "",
        contactDetails: [{
            phoneNumber: '',
            email: '',
            socialProfile: ''
        }],
        placeOfResidence: "",
        dateOfBirth: "",
        driverLicence: "",
        sex:"Не указано",
        coursesAndTrainings: [{
            courseDuration: "",
            courseEducationalInstitution: "",
            courseName: "",
            yearOfCourseEnding: ""
        }],
        workExperience:[{
            beginningOfWork: "",
            endOfWork: "",
            organization: "",
            position: "",
            responsibilitiesAndAchievements: ""
        }],
        education:[{
            educationalInstitution: "",
            degree: "",
            qualification: "",
            speciality: "",
            yearOfEndingEducationalInstitution: ""
        }],
        languages: [{
            languageName: "",
            rating:"5"
        }],
        hobbies:"",
        personalQualities:"",
        additionalInformation:""
    };
    let formData = new FormData();;
    // formData.append("personalImage", blobImage)
    // console.log(formData)
    // formData.append("formData", initialData)

    let currentData = null;
    const [data, setData] = useState(initialData);

    const handleChange = useCallback((key, value) => {
        setData((prevData)=>({...prevData, [key]:value}));
    }, [])
    useEffect(()=>{
        currentData = data;
        console.log(currentData)
    }, [data]);
    for ( let key in data) {
        if (typeof data[key] === 'object'){
            formData.append(key, JSON.stringify(data[key]));
        } else {
            formData.append(key, data[key]);
        }

    }
    console.log(formData)
    let context = useContext(EmptyInputs)


    let post = function (address, requestBody){
        return fetch(`${address.replace(/^\//g, '')}`, {
            method: 'POST',
            body:requestBody,
        }).then(response => {
            return response;
        }).catch();
    }

    let executeSubmit = function () {
        let imageSrc =
        formData.append("personalImage", )
        console.log(formData)
        for (let value of formData.values()) {
            console.log(value);
        }
        post('https://cv.renault-belarus.by/server/test', formData)
        validateAllFields(data, context)
    }

    return (

        <div className={'App'}>

            {/*<Checkbox label={123} options={[1, 'weqe', 43]}/>*/}
            <MainData initialData={initialData} handleChange={handleChange} data={data}/>
            <ContactDetails handleChange={handleChange} data={data}/>
            <PersonalData handleChange={handleChange} data={data}/>
            <WorkExperience handleChange={handleChange} data={data}/>
            <Education handleChange={handleChange} data={data}/>
            <CoursesAndTrainings handleChange={handleChange} data={data}/>
            <LanguageSkills handleChange={handleChange}  data={data}/>
            <ComputerSkills handleChange={handleChange} data={data}/>
            <AdditionalInformation handleChange={handleChange} data={data}/>
            <button className={'submitButton'} onClick={(e) => {
                executeSubmit();
            }} ></button>
        </div>

    );
}

export default App;